/* Bootstrap Adjuster */

:root {
	--vk-size-text-xs: 11px;
	--vk-color-link-text: #c00;
	--vk-color-link-text-hover: #c00;
	--vk-color-text-body: #333;
	--vk-color-text-light: #666;
	--vk-color-border-hr: #e5e5e5;
	--vk-line-height-low: 1.5em;
}

