/*-------------------------------------------*/

/* .card
/*-------------------------------------------*/

/* .card-noborder
/*-------------------------------------------*/

.vk_post { /*
	Basicly wright to without .vk_post.
	This additional is can't over wright only.
	 */
}

.vk_post.card {
	padding: 0;
	margin-bottom: 30px;
	box-sizing: border-box;
	max-width: 100%;
}

.vk_post.card .vk_post_btnOuter {
	width: calc(100% - 2.5rem);
}

/*-------------------------------------------*/

/* .card
/*-------------------------------------------*/

.card {
	overflow: hidden;
	box-shadow: none;
}

.card-meta {
	font-size: 12px;
}

.card-img-use-bg {
	position: relative;
	left: -9999px;
}

.card a {
	color: inherit;
}

.card a.btn-primary {
	color: #fff;
}

/*-------------------------------------------*/

/* .card-noborder
/*-------------------------------------------*/

:root {
	--vk-color-border-hr: #e5e5e5;
}

.card-noborder.card { /*.card.card-noborder にしないと負ける */
	border: none;
	background: none;
}

.card-noborder .vk_post_imgOuter {
	border: 1px solid var(--vk-color-border-hr);
}

.card-noborder .card-body {
	padding-left: 0;
	padding-right: 0;
	padding-bottom: 0;
}

.card-noborder.vk_post-btn-display .card-body {
	padding-bottom: 2.8rem;
}

.card-noborder.vk_post-btn-display .vk_post_btnOuter {
	width: 100%;
	bottom: 0;
}

/*-------------------------------------------*/

/* .card-intext
/*-------------------------------------------*/

.card-intext .card-intext-inner {
	display: block;
	overflow: hidden;
	width: 100%;
	height: 100%;
}

.card-intext .card-intext-inner .vk_post_body {
	position: absolute;
	display: block;
	overflow: hidden;
	width: 100%;
	bottom: 0;
	height: calc(15px + var(--vk-line-height-low) + 15px);
	transition: all 0.3s ease-out;
	background-color: rgba(0, 0, 0, 0.8);
	z-index: 100;
}

.card-intext .card-intext-inner .vk_post_body .vk_post_title {
	color: #fff;
	display: block;
	overflow: hidden;
	line-height: var(--vk-line-height-low);
	width: 100%;
	white-space: nowrap;
	text-overflow: ellipsis;
	margin-bottom: 15px;
}

.card-intext .card-intext-inner .vk_post_body p,
.card-intext .card-intext-inner .vk_post_body div {
	color: #fff;
}

.card-intext .card-intext-inner:hover .vk_post_body {
	transition: all 0.3s ease-out;
	height: 100%;
}

.card-intext .card-intext-inner:hover .vk_post_body .vk_post_title {
	height: auto;
	margin-bottom: 0;
	white-space: unset;
	text-overflow: unset;
}

.card-intext .vk_post_body,
.card-intext.vk_post-btn-display .vk_post_body {
	padding: 15px 20px;
}

/*-------------------------------------------*/

/* .card-imageRound
/*-------------------------------------------*/

.card-imageRound .vk_post_imgOuter {
	margin-left: auto;
	margin-right: auto;
	width: 150px;
	height: 150px;
	border-radius: 50%; /* 角丸半径を50%にする(=円形にする) */
	background-position: center center; /* 横長画像の左上を基準に表示 */
}

.card-imageRound .vk_post_imgOuter::before {
	padding-top: 150px;
}

.card-imageRound .card-img-overlay::after {
	border-radius: 50%;
}

/*-------------------------------------------*/

/* １カラム カード水平 の時の下部の余白を小さく変更
/*-------------------------------------------*/

/*-------------------------------------------*/

/* .card-horizontal
/*-------------------------------------------*/

.card-horizontal-inner {
	height: 100%;
}

.card-horizontal-inner::after {
	content: "";
	display: block;
	clear: both;
}

.card-horizontal-inner-row {
	height: 100%;
}

.card-horizontal-img-right-row {
	flex-direction: row-reverse;
}

.card-horizontal-reverse .row {
	flex-direction: row-reverse;
}

.card-horizontal .card-body {
	height: 100%;
	padding-bottom: 1.5em;
}

.card-sm .card-body {
	padding: 1rem 1.2rem;
}

.card-sm .card-title {
	font-size: 0.8rem;
}

.card-sm .card-text {
	font-size: 0.8rem;
	line-height: 1.4;
}

.card-sm .card-meta {
	font-size: 0.7rem;
	line-height: 1.1;
}

/*
このファイルの元ファイルは
https://github.com/vektor-inc/vektor-wp-libraries
にあります。修正の際は上記リポジトリのデータを修正してください。
*/

.vk_post { /*
	Basicly wright to without .vk_post.
	This additional is can't over wright only.
	 */
}

.vk_post.media {
	padding: 1em 0;
	margin: 0 15px;
	border-bottom: 1px solid var(--vk-color-border-hr, #e5e5e5);
}

/*-------------------------------------------*/

/* .media new type
/*-------------------------------------------*/

.media-img {
	border: 1px solid var(--vk-color-border-hr, #e5e5e5);
	padding: 1px;
	margin-right: 1rem;
}

.vk_post .media-img {
	width: 35%;
}

.vk_post .media-body {
	width: 65%;
}

/* border-top control */

.media.vk_post { /* 
	もともと1カラムでの運用想定で最初は見出し直下なので上線は不要という理屈で上線無し下線デフォルトとなっている。
	しかしながら複数カラムでの使用が後からできるようになったので、
	基本 border-top にして :last-child にだけ追加した方がスマートなので変更したいが
	既存ユーザーへの影響が読めないので現状ママという状況。
	どこかのタイミングで要検討
	*/
}

.media.vk_post:first-child {
	border-top: 1px solid var(--vk-color-border-hr, #e5e5e5);
}

