.vk_posts-layout-postListText {
	margin-bottom: 0.8rem;
}

.postListText {
	padding: 0.8rem 0 0.7rem;
	border-bottom: 1px dotted #ccc;
	display: block;
	overflow: hidden;
	width: 100%;
}

.postListText_date {
	font-size: 1rem;
	white-space: nowrap;
	font-size: 14px;
	display: block;
	float: left;
	min-width: 115px;
	margin-right: 1em;
}

.postListText_singleTermLabel {
	padding: 0;
	display: block;
	float: left;
	min-width: 150px;
	margin-right: 1em;
	margin-top: 2px;
}

.postListText_singleTermLabel_inner {
	display: block;
	overflow: hidden;
	font-size: 12px;
	white-space: nowrap;
	padding: 0 1rem;
	line-height: 16px;
	border-radius: 2px;
	text-align: center;
}

.postListText_title {
	font-size: 14px;
	display: block;
	overflow: hidden;
	margin: 0;
	padding-top: 0.2rem;
	clear: both;
}

.postListText_title a {
	color: var(--text-color);
}

.postListText_title a:hover {
	text-decoration: underline;
}

.vk_posts .postListText_title {
	line-height: 1.6;
}

/** DOM example
entry-body
	wp-block-cover
		wp-block-cover__inner-container
*/

div[class*="__inner-container"] {
	max-width: calc( var(--vk-width-container) - var(--vk-width-container-padding) * 2);
	margin-left: auto;
	margin-right: auto;
}

.alignfull {
	width: 100vw;
	margin-left: calc(50% - 50vw);
	margin-right: calc(50% - 50vw);
}

.alignfull.wp-block-image {
	width: 100vw;
	max-width: 100vw;
}

.alignwide {
	width: calc( 100% + ( 100vw - 100% ) / 2);
	margin-left: calc( ( var(--vk-width-container) - 100vw ) / 4);
	margin-right: calc( ( var(--vk-width-container) - 100vw ) / 4);
}

.alignwide.wp-block-image {
	max-width: 100vw;
}

/* カラム（水平）で点線と余白ができる */

.vk_post .row {
	margin: 0;
}

.wp-block-image {
	max-width: 100%; /* 編集画面専用 */
}

.wp-block-image .components-resizable-box__container {
	max-width: 100%;
}

.wp-block-image figcaption {
	text-align: center;
	font-size: 0.75rem;
}

.wp-block-button__link {
	font-size: 1rem;
	padding-top: 0.5em;
	padding-bottom: 0.5em;
}

/*-------------------------------------------*/

/*  Table Block
/*-------------------------------------------*/

.wp-block-table figcaption {
	margin-top: 0.4rem;
	text-align: center;
	font-size: 0.75rem;
}

/*-------------------------------------------*/

/*  「最新の記事」ブロック
/*-------------------------------------------*/

.wp-block-latest-posts {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

.wp-block-latest-posts.is-grid li {
	padding: 0.5rem;
	border-bottom: none;
}

.wp-block-latest-posts li {
	margin: 0;
	padding: 1rem 0;
	border-bottom: 1px solid var(--vk-color-border-hr);
}

.wp-block-latest-posts li > a {
	color: var(--vk-color-text-body);
	text-decoration: none;
	display: block;
	overflow: hidden;
	margin-bottom: var(--vk-margin-meta);
}

.wp-block-latest-posts li > a:hover {
	text-decoration: underline;
}

.wp-block-latest-posts li:after {
	content: "";
	display: block;
	clear: both;
}

.wp-block-latest-posts__post-author,
.wp-block-latest-posts__post-date {
	color: var(--vk-color-text-light);
	font-size: var(--vk-size-text-sm);
}

.wp-block-latest-posts__featured-image {
	margin-bottom: var(--vk-margin-meta);
	display: block;
	width: auto;
	max-width: 100%;
}

.wp-block-latest-posts__featured-image img {
	border: 1px solid var(--vk-color-border-hr);
	max-width: 100%;
}

.wp-block-latest-posts__featured-image.alignleft {
	float: left;
}

.wp-block-latest-posts__featured-image.alignright {
	float: right;
}

.wp-block-latest-posts__post-excerpt {
	font-size: var(--vk-size-text-sm);
	margin-top: var(--vk-margin-meta);
	color: var(--vk-color-text-body);
}

/*-------------------------------------------*/

/*  「RSS」ブロック
/*-------------------------------------------*/

.wp-block-rss {
	padding: 0;
	list-style: none;
}

.wp-block-rss__item {
	margin-bottom: 0;
	padding: 0.8rem 0;
	border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.wp-block-rss__item-title {
	margin-bottom: var(--vk-margin-meta);
}

.wp-block-rss__item-title a {
	color: var(--vk-color-text-body);
	text-decoration: none;
}

.wp-block-rss__item-publish-date {
	margin-right: .5em;
}

.wp-block-rss__item-publish-date:before {
	font-family: "Font Awesome 5 Free";
	font-weight: 900;
	margin-right: 5px;
	margin-left: 0;
	content: "\f073";
}

.wp-block-rss__item-author:before {
	font-family: "Font Awesome 5 Free";
	font-weight: 900;
	margin-right: 5px;
	margin-left: 0;
	content: "\f303";
}

.wp-block-rss__item-publish-date,
.wp-block-rss__item-author {
	font-size: var(--vk-size-text-xs);
	color: var(--vk-color-text-light);
	display: inline-block;
	margin-top: .5em;
}

.wp-block-rss__item-excerpt {
	font-size: var(--vk-size-text-xs);
	color: var(--vk-color-text-light);
	margin-top: var(--vk-margin-meta);
}

.wp-block-rss.is-grid {
	justify-content: space-between;
}

.wp-block-rss.is-grid li {
	margin-left: 0;
	margin-right: 0;
}

/*-------------------------------------------*/

/*  「検索」ブロック
/*-------------------------------------------*/

.wp-block-search .wp-block-search__label {
	display: none;
}

.wp-block-search .wp-block-search__button {
	width: 33%;
	color: #fff;
	border: none;
	padding: 6px;
	font-size: 16px;
	box-shadow: none;
	text-align: center;
}

.wp-block-search input.wp-block-search__input {
	max-width: initial;
	width: 64%;
	border: 1px solid #ccc;
	padding: 6px 12px;
	font-size: 14px;
	margin-right: 0;
	margin-bottom: 0;
}

.wp-block-table thead {
	border: none;
}

.wp-block-search .wp-block-search__button {
	background-color: #007bff;
}

:root {
	--vk-width-editor-container: calc(100vw * 0.8);
	--vk-width-container-padding: 15px;
	--vk-width-editor-sidebar: 280px;
	--vk-width-editor-leftmenu: 160px;
	--vk-width-editor-leftmenu-folded: 36px;
}

body.is-fullscreen-mode .edit-post-layout:not(.is-sidebar-opened) {
